@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
:root {
  --main: #FEFEFE;
  --background: #F5F8FF;
  --blue-btn: #0179FB;
  --gray-text: #6C7691;
  --black-text: #313139;
  --light-blue-icon: #729DC0;
  --dark-blue-background: #141C2F;
  --main-dark: #1F2A48;
  --color-dark-text: #E9F1FC;
}

* {
   font-family: 'Patrick Hand', cursive; 
}

.logo {
   font-size: 26px;
}

header {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 12px 0 22px 0;
   color: var(--color-dark-text);
}

.themeButton {
   text-transform: uppercase;
   font-size: 17px;
   font-weight: 300;
}

.themeButton > div {
   display: flex;
   justify-content: center;
   align-items: center;
}
.themeButton > div > i {
   font-size: 20px;
   padding-left: 12px;
}

.search {
   padding: 8px;
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 15px;
}

.search > i {
   color: var(--blue-btn);
   font-size: 24px;
   padding-right: 8px;
   padding-left: 5px;
}

.search > .userInput {
   background: none;
   border: 0;
   outline: none;
   width: 100%;
   font-size: 14px;
}

.search > .submit {
   font-size: 16px;
   padding: 7px 13px;
   border-radius: 10px;
   border: 0;
   background: var(--blue-btn);
   color: var(--main);
}

.search > .submit:active {
   box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset, rgba(0, 0, 0, 0.2) 0px 3px 0px inset;
}

::-webkit-input-placeholder {
   color: var(--gray-text);
}

:-ms-input-placeholder {
   color: var(--gray-text);
}

::placeholder {
   color: var(--gray-text);
}
.DataCard {
   padding: 18px;
   border-radius: 15px;
   margin-top: 16px;
}

.DataCard > .nameANDlogin {
   display: flex;
}

.DataCard > .nameANDlogin > .avatar > img {
   width: 75px;
   border-radius: 50%;
}

.DataCard > .nameANDlogin > .name {
   margin-left: 16px;
}

.DataCard > .nameANDlogin > .name > p {
   padding: 0;
   margin: 0;
}

.DataCard > .nameANDlogin > .name > p:nth-child(1) {
   font-size: 19px;
}

.DataCard > .nameANDlogin > .name > p:nth-child(3) {
   font-size: 14px;
   font-weight: 100;
}

.DataCard > .nameANDlogin > .name > .username {
   color: var(--blue-btn);
}

.repoAndFollow {
   padding: 0 8px;
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-around;
   border-radius: 15px;
   margin-top: 16px;
   width: calc(100% - 18px);
}

.repoAndFollow > div {
   display: flex;
   flex-direction: column;
   align-items: center;
}

.repoAndFollow > div > p:nth-child(2) {
   margin-top: -8px;
}

.info { 
   padding-top: 15px;
   margin-bottom: -10px;
}

.info > div {
   display: flex;
   margin: 8px 0px;
}

.info > div > p {
   margin: 0;
   margin-left: 7px;
}

.info > div > i {
   font-size: 20px;
}

.notAvailable {
   color: #8E93A3;
}
body {
   background-color: var(--background);
}

.container {
   padding-left: 5%;
   padding-right: 5%;
}

@media only screen and (min-width: 600px) {
   .container {
      padding-left: 20%;
      padding-right: 20%;
   }
}

.light {
   color: var(--black-text);
   background-color: val(--main);
}


.dark {
   color: var(--color-dark-text);
   background: var(--dark-blue-background);
}

.dark-DataCard {
   background-color: var(--main-dark);
   color: var(--color-dark-text);
}

.light-DataCard {
   background-color: var(--main);
   color: var(--black-text);
}

.light-repoAndFollow {
   background-color: var(--background);
}

.dark-repoAndFollow {
   background-color: var(--dark-blue-background);
}

